import React, { useEffect } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { useForm } from "react-hook-form";
import {
  createFee,
  getFee,
  updateFee,
} from "../../../app/features/fee/fee.slice";

export default function FeeModal({ show, onClose, data, setAddAdmin }) {
  const { register, handleSubmit, setValue, reset } = useForm();

  const dispatch = useDispatch();

  function onCreateSuccess() {
    const finalData = {
      feeTypeId: 701
    };
    dispatch(getFee(finalData));
    onClose();
    reset();
  }
  useEffect(() => {
    setValue("value", data?.value || "");
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  function createFeeData(submittedData) {
    const finalData = {
      feeId: data?.feeId ? data?.feeId : 0,
      value: submittedData?.value,
      feeTypeId: 701,
      isActive: true,
    };
    if (data) {
      dispatch(updateFee({ finalData, onCreateSuccess }));
    } else {
      dispatch(createFee({ finalData, onCreateSuccess }));
    }
  }
  return (
    <>
      <Modal
        show={show}
        onHide={onClose}
        size="md"
        centered
        aria-labelledby="contained-modal-title-vcenter"
        backdrop="static"
      >
        <form onSubmit={handleSubmit(createFeeData)}>
          <Modal.Header closeButton>
            <Modal.Title style={{ fontSize: "19px", fontWeight: "bold" }}>
              {data ? "Edit" : "Add"} Fees
            </Modal.Title>
          </Modal.Header>
          <Modal.Body className="p-4">
            <Row>
              <Col lg={12}>
                <Form.Group controlId="formFee">
                  <Form.Label className="fw-bold">Fee (€)</Form.Label>
                  <Form.Control
                    size="lg"
                    type="number"
                    placeholder="Enter Fee"
                    min={0}
                    {...register("value", { required: !data })}
                    // ref={register({ required: true })}
                  />
                </Form.Group>
              </Col>
            </Row>
          </Modal.Body>
          <Modal.Footer className="Doctor-Modal-Footer">
            <Button onClick={onClose} className="Admin-Modal-CancelBtn fw-bold">
              Cancel
            </Button>
            <Button
              variant="primary"
              type="submit"
              className="Admin-Add-btn fw-bold"
            >
              {data ? "Update" : "Add"} Fees
            </Button>
          </Modal.Footer>
        </form>
      </Modal>
    </>
  );
}
