import React, { useEffect, useState } from "react";
import { Card } from "react-bootstrap";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import "react-datepicker/dist/react-datepicker.css";
import { BiEdit } from "react-icons/bi";
import { useDispatch, useSelector } from "react-redux";
import FeeModal from "./FeeModal";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import { ConfirmationModal } from "../../Modal/ConfirmationModal";
import { getAllFee } from "../../../app/features/fee/fee.slice";
import useDebouncedSearch from "../../../hook/useDebouncedSearch";
import SearchInput from "../../SearchInput";

export default function FeesDataTable() {
  const [addAdmin, setAddAdmin] = useState({
    data: null,
    show: false,
  });
  const [open, setOpen] = useState(false);
  const { isLoading } = useSelector((state) => state.userRole);
  const { allFeeData } = useSelector((state) => state?.fee);
  const [searchValue, handleSearch] = useDebouncedSearch(500);

  const dispatch = useDispatch();

  useEffect(() => {
    const finalData = {
      pageNo: 0,
      size: 0,
      isPagination: false,
    };
    dispatch(getAllFee(finalData));
  }, [dispatch]);

  const feeColumns = [
    {
      dataField: "feeId",
      text: "ID",
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "value",
      text: "Fees",
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
    },
    {
      dataField: "createdDate",
      text: "Created Date",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        const date = new Date(cell);
        const day = date.getDate().toString().padStart(2, "0");
        const month = (date.getMonth() + 1).toString().padStart(2, "0");
        const year = date.getFullYear();
        return `${month}/${day}/${year}`;
      },
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row, cell) => {
        return (
          <>
            <BiEdit
              style={{ color: "#1A1A1A", cursor: "pointer" }}
              onClick={() => setAddAdmin({ data: cell, show: true })}
            />
            {/* <RiDeleteBin6Fill
              style={{ color: "red", cursor: "pointer" }}
              className="ms-3"
              onClick={() => {
                setDeleteId(cell?.userId);
                setOpen(true);
              }}
            /> */}
          </>
        );
      },
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };
  return (
    <div className="doctor-patient-paymentdetails">
      <div className="d-flex justify-content-between mb-4">
        <h5 className="font-weight-normal mb-0 d-flex align-items-center">
          Doctors Fee
        </h5>

        <button
          className=" Add-Admin rounded"
          onClick={() => setAddAdmin({ data: null, show: true })}
        >
          Add Fees
        </button>
      </div>
      <Card className="user-details-card">
        <div className="px-4 pt-3 d-flex justify-content-between custom-row align-items-center table-header-border">
          <SearchInput onChange={handleSearch} className="mb-3" />
        </div>
        {isLoading ? (
          <FullScreenLoader />
        ) : (
          <span className="doctor-datatable">
            <BootstrapTable
              columns={feeColumns}
              data={allFeeData ? allFeeData : []}
              keyField="feeId"
              id="bar"
              pagination={paginationFactory(paginateOptions)}
              bordered={false}
              wrapperClasses="table-responsive"
              sort={{ dataField: "feeId", order: "desc" }}
            />
          </span>
        )}
      </Card>
      <ConfirmationModal
        show={open}
        onHide={() => setOpen(false)}
        heading={"Delete Fee"}
        title={"this fee"}
      />

      <FeeModal
        data={addAdmin.data}
        show={addAdmin.show}
        setAddAdmin={setAddAdmin}
        onClose={() => setAddAdmin({ show: false, data: null })}
      />
    </div>
  );
}
